<template>
  <div id="listenInternetOne">
    <div class="one-form">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="180px" class="demo-ruleForm" label-position="left">
        <el-form-item label="标题" prop="headlineTitle">
          <el-input v-model="ruleForm.headlineTitle"></el-input>
        </el-form-item>
        <el-form-item label="请输入您要测试的URL" prop="testUrl">
          <el-input v-model="ruleForm.testUrl"></el-input>
        </el-form-item>
        <el-form-item label="测试模式">
          <el-radio-group v-model="radioTestMethod">
            <el-radio-button label="get">GET</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="测试频率">
            <el-radio-group v-model="radioFrequency">
              <el-radio-button label="1">1秒</el-radio-button>
              <el-radio-button label="2">2秒</el-radio-button>
              <el-radio-button label="5">5秒</el-radio-button>
              <el-radio-button label="7">7秒</el-radio-button>
              <el-radio-button label="60">1分钟</el-radio-button>
            </el-radio-group>
        </el-form-item>
        <el-form-item  prop="name">
          <el-col :span="2" class="inline">
            自定义：
          </el-col>
          <el-col :span="4">
            <el-form-item prop="minuteNumber">
              <el-input v-model.number="ruleForm.minuteNumber" type="number"  min="0"></el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">分钟</el-col>

        </el-form-item>
        <el-form-item label="节点数量">
          <el-radio-group v-model="radioNodeNumber">
            <el-radio-button label="100"></el-radio-button>
            <el-radio-button label="200"></el-radio-button>
            <el-radio-button label="500"></el-radio-button>
            <el-radio-button label="1000"></el-radio-button>
            <el-radio-button label="5000"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-col :span="2" class="inline">
            自定义：
          </el-col>
          <el-col :span="4">
            <el-form-item prop="nodeNumber">
              <el-input v-model.number="ruleForm.nodeNumber" type="number" min="0"></el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="12">（最大节点数量不可超过{{ nodeAmount }}个）</el-col>
        </el-form-item>
        <el-form-item label="测试时间范围">
          <el-radio-group v-model="radioTestTimeRange">
            <el-radio-button label="1800">30分钟</el-radio-button>
            <el-radio-button label="3600">1小时</el-radio-button>
            <el-radio-button label="7200">2小时</el-radio-button>
            <el-radio-button label="18000">5小时</el-radio-button>
            <el-radio-button label="25200">7小时</el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item  label="需支付">
          <span>{{ NeedCanAmount }}</span> CAN
        </el-form-item>
        <el-form-item label="发币地址" prop="outBiAddress">
          <el-input v-model="ruleForm.outBiAddress"></el-input>
        </el-form-item>
        <el-form-item label="收币地址">
          <el-col class="inline" >
            <span>{{inBiAddress}}</span>
            <el-button>点击复制</el-button>
            <el-button>二维码</el-button>
          </el-col>
        </el-form-item>


        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">购买</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    computed: {
      radioFrequencyComputed () {
        if (this.ruleForm.minuteNumber) {
          this.radioFrequency = this.ruleForm.minuteNumber * 60
          return this.radioFrequency
        } else {
          return this.radioFrequency
        }
      },
      radioNodeNumberComputed () {
        if (this.ruleForm.nodeNumber) {
          this.radioNodeNumber = this.ruleForm.nodeNumber
          return this.radioNodeNumber
        } else {
          return this.radioNodeNumber
        }
      },
      NeedCanAmount () {
        let needCan, alltimes
        alltimes = (this.radioNodeNumberComputed * this.radioTestTimeRange) / this.radioFrequencyComputed
        needCan = alltimes / 5
        return needCan
      }
    },
    mounted () {
      this.getInAddressNodeMethod()
    },
    data () {
      let validateNodeNumber = (rule, value, callback) => {
        if (value === '') {
          this.radioNodeNumber = 100
        } else if (value > this.nodeAmount) {
          callback(new Error('不能大于节点数量'))
        }
      }
      return {
        radioFrequency: 1,
        radioNodeNumber: 100,
        radioTestMethod: 'get',
        radioTestTimeRange: 1800,
        inBiAddress: '',
        nodeAmount: 0,
        ruleForm: {
          headlineTitle: '',
          testUrl: '',
          minuteNumber: '',
          nodeNumber: '',
          outBiAddress: ''
        },
        rules: {
          headlineTitle: { required: true, message: '请输入标题', trigger: 'blur' },
          testUrl: { required: true, message: '请输入测试地址', trigger: 'blur' },
          nodeNumber: { validator: validateNodeNumber, trigger: 'change' },
          outBiAddress: { required: true, message: '请输入发币地址', trigger: 'blur' }
        }
      }
    },
    methods: {
      submitForm (formName) { //submit ruleForm
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let vm = this
            let sendData = {
              url: this.ruleForm.testUrl,
              method: this.radioTestMethod,
              nodeAmount: this.radioNodeNumber,
              rate: this.radioFrequency,
              time: this.radioTestTimeRange,
              name: this.ruleForm.headlineTitle,
              fromAddress: this.ruleForm.outBiAddress,
              canAmount: this.NeedCanAmount
            }
            axios.post('/tingwang/upload/project/data', sendData)
            .then(function (response) {
              console.log(response.data)
            }).catch(function (error) {
              console.log(error)
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      getInAddressNodeMethod () {
        let vm = this
        axios.get('/tingwang/get/address/nodes')
          .then(function (response) {
            console.log(response)
            vm.nodeAmount = response.data.nodeAmount
            vm.inBiAddress = response.data.receiveAddress
          }).catch(function (error) {
            console.log(error)
        })
      }
    }
  }
</script>
<style>

</style>



